.header {
  width: 100%;
  background: linear-gradient(150deg, #ffb8ca, #ae586e, #a33752) 0 0 / 100% 300%;
  animation: fondo 6s ease-in infinite alternate-reverse;
  position: relative;
}

.header .header__nav {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 30px 25px 0;
  display: flex;
}

@keyframes fondo {
  0% {
    background-color: #ffb8ca;
    background-position: 0%;
  }

  50% {
    background-color: #ae586e;
    background-position: 100%;
  }

  100% {
    background-color: #a33752;
    background-position: 0%;
  }
}

